.c-about-page-top {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 15vh;
}

.c-about-section {
    display: flex;
    /* height: 65vh; */
    align-items: center;
    justify-content: center;
    flex-direction: row;
}

.c-contact-section {
    display: flex;
    /* height: 65vh; */
    align-items: center;
    justify-content: center;
    flex-direction: row;
    padding-top: 8vh;
    padding-bottom: 8vh;
}

.c-about-section-img-img {
    width: 100%;
    height: auto;
}

.c-contact-section-img-img {
    width: 100%;
    height: auto;
}

.c-about-section-img1 {
    width: 50vw;
    height: 100vh;
    pointer-events: none;
    flex: 1;
    overflow: auto;
    z-index: -1; /* Remove this line if it's not going to be a background! */
    background-image: url('../icons/about.jpg');
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Do not repeat the image */
    background-size: contain; /* Resize the background image to cover the entire container */
  }

  .c-contact-section-img1 {
    width: 50vw;
    height: 100vh;
    pointer-events: none;
    flex: 1;
    overflow: auto;
    z-index: -1; /* Remove this line if it's not going to be a background! */
    background-image: url('../icons/contactus.jpg');
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Do not repeat the image */
    background-size: contain; /* Resize the background image to cover the entire container */
  }

  .c-about-section-text {
    width: 38.5vw;
    /* height: 48vh; */
    padding: 4vh;
    background: white;
    border-left-width: 0;
    border-right-width: 0;
    position: relative;
    display: block;
    margin-left: auto;
    margin-right: auto;
    border-left-color: transparent;
    border-right-color: transparent;
    border-left-style: solid;
    border-right-style: solid;
    border-top: 0 solid transparent;
    border-bottom: 0 solid transparent;
    color: #2f2f2f;
}

.c-contact-section-text {
    width: 38.5vw;
    /* background: white; */
    border-left-width: 0;
    border-right-width: 0;
    position: relative;
    display: block;
    margin-left: auto;
    margin-right: auto;
    border-left-color: transparent;
    border-right-color: transparent;
    border-left-style: solid;
    border-right-style: solid;
    border-top: 0 solid transparent;
    border-bottom: 0 solid transparent;
    color: #2f2f2f;
}
.c-about-section-bottom {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
    margin-right: 8vh;
    /* height: 50vh; */
}

.c-contact-section-bottom {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
    margin-right: 8vh;
    /* height: 50vh; */
}




.c-about-section-h3 {
    line-height: 1.5;
    font-weight: 500;
    margin-bottom: .5em;
}

.c-contact-section-h3 {
    line-height: 1.5;
    font-weight: 500;
    margin-bottom: .5em;
    padding-left: 10px;
    background: white;
    margin: 0;
}

.c-about-section-p {
    margin-bottom: 0;
    opacity: .8;
    font-size: calc(.75rem + (56.25vw - 27em)/72);
    line-height: 1.81;
    font-weight: 400;
    color: #2f2f2f;
}



/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 768px) {
    .c-about-page-top {
        padding-top: 0vh;
    }

    .c-about-section {
        /* height: 100vh; */
        margin-top: 15vh;
        flex-direction: column;
    }

    .c-contact-section {
        /* height: 100vh; */
        flex-direction: column;
        padding-top: 8vh;
    }

    .c-about-section-img {
        width: 90vw;
    }

    .c-contact-section-img {
        width: 90vw;
    }

    .c-about-section-text {
        width: 90vw;
    }

    .c-contact-section-text {
        /* height: 48vh; */
        width: 90vw;
        padding-bottom: 4vh;
    }

    .c-about-section-bottom {
        margin: 0vh;
    }
    
    .c-contact-section-bottom {
        margin: 0vh;
    }
}

