.main-content {
  flex: 1 0 auto;
}

.c-main-navigation {
  display: flex;
  flex-direction: column;
  top: 1rem;
  right: 1rem;
  position: fixed;
  justify-content: flex-end;
  flex-shrink: 0;
  z-index: 1000;
}


.c-tb-content {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.work-area {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row wrap;
}

.c-nav-item {
    display: inline-block;
    margin: 0 16px;
    position: relative;
    transition: color .2s linear,opacity .2s linear;
    font-size: 18px;
    font-weight: 600;
}