.c-products-page {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15vh 4vh 4vh 4vh;
    height: 100vh;
}

.c-products-page-carousel {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60vw;
    /* height: 60vh; */
}

/* .c-products-page-carousel  .carousel-inner {
    height: 100vh;
}
 */
.c-products-page > .carousel-control-next {
    opacity: 2;
}

.c-products-page .carousel-control-next-icon {
    width: 30px !important;
    height: 30px !important;
}

.c-products-page .carousel-control-prev-icon {
    width: 30px !important;
    height: 30px !important;
}

.c-products-page .carousel-caption {
    top: 15% !important;
    color: #212529 !important;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .c-products-page {
        flex-direction: column;
        padding: 0vh 4vh 4vh 4vh;
        height: auto;
    }
    .c-products-page-carousel {
        width: 90vw;
    }
    .c-contact-form-captcha-parent {
        flex-direction: column;
    }
}

