.logo-top {
  display: flex;
  position: fixed;
  left: 1rem;
  top: 1rem;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.c-company-logo {
  max-height: 100px;
  max-width: 300px;
  pointer-events: none;
}
  
.c-company {
  display: inline-block;
  vertical-align: top;
  padding-left: 5px;
  flex-grow: 1;
}
  