.c-home-section-top {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 15vh;
}

.c-home-section {
    display: flex;
    height: 65vh;
    align-items: center;
    justify-content: center;
    flex-direction: row;
}

.c-home-section-img {
    width: 50vw;
    height: 100vh;
    pointer-events: none;
    flex: 1;
    overflow: auto;
    z-index: -1; /* Remove this line if it's not going to be a background! */
    background-image: url('../icons/landing.jpg');
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Do not repeat the image */
    background-size: contain; /* Resize the background image to cover the entire container */
  }

  .c-home-section-bottom {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
    height: 50vh;
}

.c-home-section-h3 {
    font-size: calc(1.5rem + (75vw - 36em)/72);
    line-height: 1.5;
    font-weight: 500;
    margin-bottom: .5em;
}

.c-home-section-text {
    width: 38.5vw;
    /* height: 48vh; */
    padding: 8vh;
    background: white;
    border-left-width: 0;
    border-right-width: 0;
    position: relative;
    display: block;
    margin-left: auto;
    margin-right: auto;
    border-left-color: transparent;
    border-right-color: transparent;
    border-left-style: solid;
    border-right-style: solid;
    border-top: 0 solid transparent;
    border-bottom: 0 solid transparent;
    color: #2f2f2f;
}

.c-home-section-p {
    margin-bottom: 0;
    opacity: .8;
    font-size: calc(.75rem + (56.25vw - 27em)/72);
    line-height: 1.81;
    font-weight: 400;
    color: #2f2f2f;
}

.c-home-section-bottom-btn {
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.c-home-section-bottom-btn .btn-primary {
    background-color: #094275;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .c-home-section-top {
        height: 100vh;
        padding-top: 0vh;
    }

    .c-home-section {
        flex-direction: column;
        height: auto;
    }

    .c-home-section-img {
        width: 90vw;
        flex: 1 0 100%;
    }

    .c-home-section-h3 {
        font-size: calc(1.5rem + (75vw - 36em)/72);
        line-height: 1.5;
        font-weight: 500;
        margin-bottom: .5em;
    }

    .c-home-section-text {
        width: 90vw;
        padding: 4vh;
        height: auto;
    }

    .c-home-section-p {
        font-size: large;
        margin-bottom: 5px;
    }
}

